
const login = async (username, password) => {
    const reqOpts = {
        method: 'POST',
        headers: {
            'Content-Type' : 'application/json'
        },
        body: JSON.stringify({ username, password }),
    }

    const resp = await fetch('/auth/obtain', reqOpts).then(data => data.json());

    return resp;
}



export {
    login,
}









